<template>
  <div id="app">
    <div>
      <Particles v-if="isReady" v-bind:properties=getParticlesProps() 
                v-bind:parameters=getParticlesParams() ref="particles">
      </Particles>
    </div>
    <div v-bind:class="{ hidden: !menuIsShowing }" class="popup-holder">
      <div class="top-right icon-holder-popup" v-on:click="clickHideMenu()"><i class="material-icons">close</i></div>
      <div class="mini-heading">EPIDEMIC SIMULATOR</div>
      <h2>{{menuItems[menuItemNo].title}}</h2>
      <div class="popup-content" ref="pholder" >
        <div class="slider-holder" ref="sh">
        <template v-if="menuItemNo===0">
          <template v-if="!isReady"></template>
          <template v-if="isReady">
              <Slider v-for="params in getSliderParams()" v-bind:key="params.key" 
                  v-bind:parameters="params" v-bind:properties="getSliderProps(params)"></Slider>
          </template>
        </template>
        </div>
        <div v-if="menuItemNo===1">
          <p>Blahblahblah.</p>
        </div>
        <div v-if="menuItemNo===2">
          <div v-if="isReady">
            <Graph></Graph>
          </div>
        </div>
      </div>
    </div>
    <div class="reset-button icon-holder"> 
      <div v-on:click="clickReset()"><i class="material-icons">refresh</i></div>
    </div>
    <div v-if="!menuIsShowing" class="top-right icon-holder">
      <div v-on:click="clickShowMenu(0)"><i class="material-icons">settings</i></div>
      <div v-on:click="clickShowMenu(1)"><i class="material-icons">info</i></div>
      <div v-on:click="clickShowMenu(2)"><i class="material-icons">timeline</i></div>
    </div>
  </div>
</template>

<script>
import Particles from './components/Particles.vue';
import Slider from './components/Slider.vue';
import Graph from './components/Graph.vue';
import paper from 'paper';

export default {
  name: 'App',
  components: {
    Particles, Slider, Graph
  },
  data: function(){
    return {
      isReady: false,
      params: this.initializeParams(),
      resetTrigger: false,
      menuIsShowing: false,
      menuItemNo:2,
      menuItems:[
        {iconName:'settings', title:'Controls'},
        {iconName:'info', title:'About'},
        {iconName:'timeline', title:'"The Curve"'}
      ]
    }
  },
  methods: {
    initializeParams() {
      //This is necessary because we want to create a new 'resetValue' variable based on 'value'.
      let params = [
        {'key':'average_speed', 'title': 'Average Speed', 'value': 0.5, 'min': 0, 'max': 2 },
        {'key':'speed_range', 'title': 'Speed Range', 'value': 0.5, 'min': 0, 'max': 1.0 },
        {'key':'transmission_distance', 'title': 'Transmission Distance', 'value': 5, 'min': 0, 'max': 20 },
        {'key':'transmission_probability', 'title': 'Transmission Probability', 'value': 0.1, 'min': 0, 'max': 1.0 }
      ]
      params.forEach(param => {
        param.resetValue = param.value;
      });
      return params;
    },
    getParticlesProps(){
      return {
        id: 'particles',
        width: 0.99 * window.innerWidth,
        height: 0.99 * window.innerHeight,
        isReady: this.isReady
      }
    },
    getSliderProps(params){
      let props = Object.assign(params, {
        id: params.key + '-slider',
        width: this.$refs.sh.offsetWidth * 0.8,
        height: this.$refs.sh.offsetHeight / 5,
        weights: {
          paddingTop: 2,
          paddingBottom: 4,
          titleFontSize: 5,
          paddingMiddle: 3,
          slotWidth: 4
        },
      });
      return props;
    },
    getSliderParams(){
      return this.params;
    },
    getParticlesParams(){
      let particlesParams = {}
      this.params.forEach(param => {
        particlesParams[param.key] = param;
      });
      return particlesParams;
    },
    clickReset(){
      this.$refs.particles.triggerReset();
    },
    clickShowMenu(menuItemNo){
      this.menuIsShowing = true;
      this.menuItemNo = menuItemNo;
    },
    clickHideMenu(){
      this.menuIsShowing = false;
    }
  },
  mounted: function(){
    paper.install(window);
    this.isReady = true;

    console.log(this.$refs.sh.offsetWidth)
  }
}
</script>

