<template>
    <canvas :id="properties.id" data-paper-hidpi="off"></canvas>
</template>
<script>
export default {
    props: ['properties', 'parameters'],
    mounted: function(){
        let params = this.parameters;
        let holder = this.$refs.sh;
        let canvas = document.getElementById(this.properties.id);
        console.log(this.properties)
        canvas.width = this.properties.width;
        canvas.height = this.properties.height;
        paper.setup(canvas);

        
        let weights = this.properties.weights;
        let yMultiplier = canvas.height / (weights.paddingTop + weights.paddingBottom + weights.paddingMiddle + weights.titleFontSize + weights.slotWidth);

        let paddingTop = yMultiplier * weights.paddingTop;
        let paddingBottom = yMultiplier * weights.paddingBottom;
        let titleFontSize = yMultiplier * weights.titleFontSize;
        let paddingMiddle = yMultiplier * weights.paddingMiddle;
        let slotWidth = yMultiplier * weights.slotWidth;


        let yTitle = paddingTop + titleFontSize;
        let ySlotTop = yTitle + paddingMiddle;
        let xTitle = 0 * slotWidth;



        let title = new PointText(new Point(xTitle, yTitle));
        title.justification = 'left';
        title.fillColor = 'black';
        title.fontFamily = "Open Sans";
        title.fontSize = titleFontSize;
        let updateTitle = function(text) {
            title.content = params.title + ': ' + Math.round(params.value * 100)/100;
        }
        updateTitle()
        
        let ySlot = ySlotTop + slotWidth / 2;
        let xSlotLeft = slotWidth / 2;
        let xSlotRight = canvas.width - slotWidth / 2;
        let slotColor = 'black'


        let slot = new Path.Line(new Point(xSlotLeft, ySlot), new Point(xSlotRight, ySlot));
        slot.style = {
            strokeColor: slotColor,
            strokeWidth: slotWidth,
            strokeCap: 'round'
        }
        slot.style.strokeColor = slotColor;
        slot.style.strokeWidth = slotWidth;


        let rangeMin = xSlotLeft;
        let rangeMax = xSlotRight;

        let scaleFactor = (rangeMax - rangeMin) / (params.max - params.min);

        let rangeToDomain = function(r){
            let d = (params.max - params.min) * (r - rangeMin) / (rangeMax - rangeMin);
            return d;
        }

        let domainToRange = function(d){
            let r = rangeMin + scaleFactor * (d - params.min);
            return r;
        }


        let handleRadius = slotWidth * 1.25;
        let handleStrokeWidth = handleRadius / 10;
        let handleFillColor = 'white';
        let handleStrokeColor = 'black';

        let xHandle = domainToRange(params.value);


        let handle = new Path.Circle(xHandle, ySlot, handleRadius);
        handle.style = {
            strokeColor: handleStrokeColor,
            strokeWidth: handleStrokeWidth,
            fillColor: handleFillColor
        }

        let xDragStart = null;

        handle.onMouseDown = function(event) {
            xDragStart = event.point.x
        }

        handle.onMouseDrag = function(event) {
            let xCurrent = event.point.x;
            let newHandlePosition = handle.position.x + xCurrent - xDragStart;
            handle.position.x = newHandlePosition < rangeMin ? rangeMin : newHandlePosition > rangeMax ? rangeMax : newHandlePosition;
            xDragStart = xCurrent;
            params.value = rangeToDomain(handle.position.x);
            updateTitle();
        }




    }
}
</script>
