<template>
    <canvas :id=properties.id data-paper-hidpi="off">
    </canvas>
</template>
<script>
import { bus } from '../main';
export default {
    props:['properties', 'parameters'],
    methods: {
        triggerReset: function() {
            this.resetRequired = true;
        }
    },
    mounted: function(){
        this.resetRequired = false;
        const IS_RECOVERED = -1;
        const IS_DEAD = -2;
        const IS_UNINFECTED = -3;
        let params = this.parameters;
        let canvas = document.getElementById(this.properties.id);
        canvas.width = this.properties.width;
        canvas.height = this.properties.height;
        paper.setup(canvas);

        let rect = new Path.Rectangle({
            point: [0, 0],
            size: [canvas.width, canvas.height],
        });
        
        rect.fillColor = '#ddeeff';

        let layer = project.activeLayer;
        
        let getOuterRadius = function(){
            return params['transmission_distance'].value * canvas.width / 100;
        }
        let getTransmissionProbability  =function(){
            return params['transmission_probability'].value;
        }

        let xMin = 0;
        let xMax = canvas.width;
        let xRange = xMax - xMin;
        let yMin = 0;
        let yMax = canvas.height;
        let yRange = yMax - yMin;

        let transmissionProbability = getTransmissionProbability();
        let infectionPeriod = 200;
        let deathProbability = 0.1 / infectionPeriod;

        let nPoints = 100;
        let nInfectedInitial = 1;
        let points = [];
        let interactions = [];

        let innerRadius = Math.min(canvas.height, canvas.width) / 100;
        let outerRadius = getOuterRadius()
        let previousOuterRadius = outerRadius;

        let time = 0;
        let timePrevious = 0;

        let getInteractionState = function(positionA, positionB, interactionRange) {
            let xDistance = Math.abs(positionA.x - positionB.x);
            let yDistance = Math.abs(positionA.y - positionB.y);
            if(xDistance > interactionRange || yDistance > interactionRange) return false;
            return ((xDistance ^ 2) + (yDistance ^ 2) < (interactionRange ^ 2));
        }

        let handleInteraction = function(pointA, pointB, areInteracting) {
            let bothPoints = [pointA, pointB];
            let infectedPoints = [];
            let infectablePoints = [];
            if(areInteracting){
                bothPoints.forEach(point => {
                    point.isInteracting = true;
                    if(point.timeToRecovery >= 0) {
                        infectedPoints.push(point);
                    } else if(point.timeToRecovery === IS_UNINFECTED) {
                        infectablePoints.push(point);
                    }
                });
                if(infectedPoints.length === 1 && infectablePoints.length === 1) {
                    infectablePoints[0].timeToRecovery = Math.random() < transmissionProbability ? infectionPeriod : infectablePoints[0].timeToRecovery;
                    infectedPoints[0].mayInfectImminently = true;
                }
            }

            bothPoints.forEach(point => {
                setInteractionStyle(point);
                point.innerCircle.style = Object.assign(getInnerCircleStyle(point.timeToRecovery), point.innerCircle.style);
            });
        }
        
        let getInnerCircleStyle = function(timeToRecovery) {
            let unit = 0.1 * innerRadius;
            let style = {
                fillColor: '#e74c3c',
                strokeWidth: 2* unit,
                strokeColor: '#c0392b'
            };
            if(timeToRecovery === IS_DEAD) {
                style.fillColor = '#2c3e50';
                style.strokeColor = 'grey';
            } else if(timeToRecovery === IS_RECOVERED) {
                style.fillColor = '#f1c40f';
                style.strokeColor = '#f39c12';
            } else if(timeToRecovery === IS_UNINFECTED) {
                style.fillColor = 'white';
                style.strokeColor = 'black';
            }
            return style;
        }

        let setInteractionStyle = function(point) {
            let outerCircle = point.outerCircle;
            let unit = innerRadius * 0.2;
            outerCircle.opacity = 1;
            let style =  {
                strokeColor: 'blue',
                strokeWidth: 0,
                dashArray: [1, 2],
                fillColor: null
            }
            if(point.isInteracting){
                if(point.mayInfectImminently){
                    outerCircle.opacity = 0.5;
                    style = {
                        strokeColor: '#c0392b',
                        strokeWidth: 2 * unit,
                        dashArray: [5 * unit, 5 * unit],
                        fillColor: 'lightgray'
                    };
                } else {
                    style= {
                        strokeColor: '#2980b9',
                        strokeWidth: unit,
                        dashArray: [1, 5],
                        fillColor: null
                    }
                }
            }
            point.outerCircle.style = style;
        }

        let updateSpeed = function(index) {
            let speed = params['average_speed'].value;
            let range = params['speed_range'].value;
            //if(speed == 0) range = 0;
            if(nPoints > 1) {
                let minSpeed = speed * (1 - range);
                let speedIncrement = (2 * range * speed) / (nPoints - 1);
                speed = minSpeed + (index * speedIncrement);
            }
            //console.log(index + ": " + speed)
            return speed;
        }

        let updateCircles = function() {
            outerRadius = getOuterRadius();
            points.forEach(point => {
                let x = 0;
                let y = 0;

                if(!point.innerCircle){
                    //Create circles for first time                
                    x =  xMin + Math.random() * xRange;
                    y =  yMin + Math.random() * yRange;
                    let innerCircle = new Path.Circle(x, y, innerRadius);
                    layer.addChild(innerCircle);
                    point.innerCircle =  innerCircle;
                    point.innerCircle.style = getInnerCircleStyle(point.timeToRecovery);
                    point.direction = Math.random() * 360;
                } else {
                    x = point.innerCircle.position.x;
                    y = point.innerCircle.position.y;
                    point.outerCircle.remove();
                }
                //Use x and y of innerCircle to redraw outerCircle,
                let outerCircle = new Path.Circle(x, y, outerRadius);
                layer.addChild(outerCircle);
                outerCircle.insertAbove(rect);
                point.outerCircle = outerCircle;
            })
        }

        let drawPoints = function() {
            for(let i = 0; i < nPoints; i++){
                let point = {
                    index: i,
                    speed: updateSpeed(i),
                    isInteracting: false,
                    timeToRecovery: i < nInfectedInitial ? infectionPeriod : IS_UNINFECTED,
                    mayInfectImminently: false
                }
                points.push(point);
            }
            updateCircles();
        }

        let clearPoints = function() {
            points.forEach(point => {
                point.innerCircle.remove();
                point.outerCircle.remove();
            });
            points = [];
        }

        

        drawPoints();
        let self = this;
        let resetTime = 0;
        let resetCount = 0

        view.onFrame =function(event) {
            
            time = event.time;
            if(self.resetRequired){
                clearPoints();
                drawPoints();
                self.resetRequired = false;
                resetCount++;
                resetTime = time;
            }
            if(points.length === nPoints){
                let newCases = 0;
                transmissionProbability = getTransmissionProbability();
                points.forEach((point, i) => {
                    point.speed = updateSpeed(i);
                    outerRadius = getOuterRadius()
                    if(previousOuterRadius != outerRadius){
                        updateCircles();
                        previousOuterRadius = outerRadius;
                    }
                    let timeToRecovery = point.timeToRecovery;
                    if(timeToRecovery >= 0) {
                        if(Math.random() < deathProbability){
                            point.timeToRecovery = IS_DEAD;
                            point.innerCircle.insertAbove(rect);
                            Object.assign(getInnerCircleStyle(point.timeToRecovery), point.innerCircle.style);
                        } else {
                            point.timeToRecovery--;
                        }
                    }

                    let nInteractions = 0;
                    point.isInteracting = false;
                    point.mayInfectImminently = false;
                    for(let j = 0; j < i; j++) {
                        let pointA = point;
                        let pointB = points[j];
                        let areInteracting = getInteractionState(pointA.innerCircle.position, pointB.innerCircle.position, outerRadius);
                        handleInteraction(pointA, pointB, areInteracting);
                    }
                    if(point.timeToRecovery === infectionPeriod){
                        newCases++;
                    } else if(point.timeToRecovery !== IS_DEAD){
                        let delta = time - timePrevious//event.delta;
                        let vector = new Point(0, 1 * delta * point.speed * 100);
                        vector.angle = point.direction//360 * Math.random();
                        let oldPosition = point.innerCircle.position;
                        let newX = oldPosition.x + vector.x;
                        let newY = oldPosition.y + vector.y;
                        if(newX < xMin){
                            newX = xMin + (xMin - newX);
                            vector.x = -vector.x;
                        } else if (newX > xMax) {
                            newX = xMax - (newX - xMax);
                            vector.x = -vector.x;
                        }
                        if(newY < yMin){
                            newY = yMin + (yMin - newY);
                            vector.y = -vector.y;
                        } else if (newY > yMax) {
                            newY = yMax - (newY - yMax);
                            vector.y = -vector.y;
                        }
                        point.direction = vector.angle + (0.5 - Math.random()) * 20;
                        point.innerCircle.position = [newX, newY]
                        point.outerCircle.position = [newX, newY]
                    }
                });
                bus.$emit('redrawn', {
                    resetCount,
                    t: time - resetTime,
                    x: newCases
                });
                timePrevious = time;
            }
        }
        view.draw();



    }
}
</script>
