<template>
<div class="graph-holder" ref="gh">
    <canvas id="graph-canvas" data-paper-hidpi="off"></canvas>
</div>
</template>
<script>
import { bus } from '../main';
export default {
    props:[],
    methods:{},
    mounted:function(){
        let canvas = document.getElementById('graph-canvas');
        let holder = this.$refs.gh
        let width = holder.offsetWidth;
        let height = holder.offsetHeight;
        canvas.width = width;
        canvas.height = height;
        paper.setup(canvas);

        let paddingRatio = 0.05;
        let padding = {
            n: 0, //height * paddingRatio,
            e: width * paddingRatio,
            s: height * paddingRatio,
            w: width * paddingRatio
        }


        let previousCurves = [];
        let maxPreviousCurves = 4;
        let bucketWidth = 2;


        let tDomainMin = 0
        let tDomainMax =  45;
        let tRangeMin = padding.w;
        let tRangeMax = width - padding.e;
        let tScaleFactor = (tRangeMax - tRangeMin) / (tDomainMax - tDomainMin);

        let xDomainMin = 0;
        let xDomainMax = 12;
        let xRangeMin = height - padding.s;
        let xRangeMax = padding.n;
        let xScaleFactor = (xRangeMax - xRangeMin) / (xDomainMax - xDomainMin);

        let getCanvasCoords = function(tDomain, xDomain) {
            return {
                t: tRangeMin + tScaleFactor * (tDomain - tDomainMin),
                x: xRangeMin + xScaleFactor * (xDomain - xDomainMin)
            }
        }

        let getNewCurve = function (xCumulativeInitial){
            let curve = new Path();
            curve.strokeColor = 'red';
            curve.strokeWidth = 3;
            //Add tZero point (cooresponds to time before first infection);
            let zeroPoint = getNewPoint(getCanvasCoords(0, 0));
            curve.add(zeroPoint);
            let currentPoint = getNewPoint(getCanvasCoords(0, xCumulativeInitial));
            curve.add(currentPoint);
            return curve;
        }

        let getNewPoint = function(coords){
            return new Point(coords.t, coords.x)
        }

        let getNewState = function(resetCount){
            return {
                resetCount: resetCount,
                xCumulative: 1,
                bucketStart: 0,
                bucketEnd: bucketWidth,
                curve: getNewCurve()
            };
        }

        let updateState = function(state){

        }

        //Draw X Gridlines
        let increment = 1;
        let xDomainGridline = 0;
        while(xDomainGridline <= xDomainMax){
            let gridline = new Path();
            gridline.strokeColor = 'lightblue';
            gridline.strokeWidth = 1;
            gridline.add(getNewPoint(getCanvasCoords(tDomainMin, xDomainGridline)));
            gridline.add(getNewPoint(getCanvasCoords(tDomainMax, xDomainGridline)));
            xDomainGridline += increment;
        }

        //Draw T Gridlines
        increment = 1;
        let tDomainGridline = 0;
        while(tDomainGridline <= tDomainMax){
            let gridline = new Path();
            gridline.strokeColor = 'lightblue';
            gridline.strokeWidth = 1;
            gridline.add(getNewPoint(getCanvasCoords(tDomainGridline, xDomainMin)));
            gridline.add(getNewPoint(getCanvasCoords(tDomainGridline, xDomainMax)));
            tDomainGridline += increment;
        }


        let state = getNewState(0);


        let bucketStart = 0;
        let bucketEnd = bucketStart + bucketWidth;
        let resetCount = 0;

        let xCumulative = 1;
        let curve = getNewCurve(xCumulative);

        
        bus.$on('redrawn', (d) => {
            if(d.resetCount !== resetCount){
                curve.strokeColor = 'black';
                previousCurves.push(curve);
                if(previousCurves > maxPreviousCurves) curves.pop();
                previousCurves.forEach(previousCurve => {
                    previousCurve.opacity = previousCurve.opacity * 0.5;
                });
                resetCount = d.resetCount;
                bucketStart = 0;
                bucketEnd = bucketStart + bucketWidth;

                xCumulative = 1
                curve = getNewCurve(xCumulative);

            }
            if(d.t < tDomainMax){
                if(d.t < bucketEnd) {
                    xCumulative += d.x;
                    curve.removeSegment(curve.segments.length - 1)
                    let currentPoint =  getNewPoint(getCanvasCoords(d.t, xCumulative))//new Point(5 *d.t, 180 - 20 * xCumulative)
                    curve.add(currentPoint);
                } else {
                    let currentPoint = getNewPoint(getCanvasCoords(d.t, 0));//new Point(5 *d.t, 180)
                    curve.add(currentPoint);

                    xCumulative = 0;

                    bucketStart = bucketEnd;
                    bucketEnd += bucketWidth
                }
            }
        });
    }
}
</script>
<style>
.graph-holder{
  position:absolute;
  top:0px;
  bottom:0px;
  left:0px;
  right:0px;
}
</style>